body {
  background-color: rgb(0, 0, 0);
  color: rgb(0, 255, 115);
  overflow-x: hidden;
}

#headshot {
  position: center;
  padding-top: 25%;
  width: 25%;
  max-width: 90%;
  height: 25%;
  max-height: 90%;
  background-image: url("../public/pics/headshot2.jpg");
  border-radius: 50%;
  background-size: cover;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  /* transform: scale(-1, 1) */
}

.grid {
  white-space: nowrap;
}

.grid-element {
  display: inline-block;
  height: 0;
  width: 3%;
  padding-bottom: 3%;
}

.grid-element .value {
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: sticky;
  z-index: 1;
  visibility: hidden;
}

.grid-element:hover .value {
  visibility: visible;
}

.grid-element:hover {
  opacity: 0.5;
}

.App {
  display: row;
}

ul {
  text-align: left;
  display: table;
  margin: 0 auto;
}

.control-box {
  padding: 1em;
  outline: 1px solid rgb(238, 194, 0);
  height: 100%;
  width: 100%;
}

.code {
  background-color: rgb(26, 26, 26);
  padding: 1em;
}

.control-box>h2 {
  text-align: center;
  color: red;
}

textarea,
input {
  background: rgb(26, 26, 26);
  color: rgb(0, 255, 115);
}

/* textarea {
  width: 100%;
  height: 5em;
} */

.applets>p {
  text-align: center;
  justify-content: center;
}

pre {
  color: rgb(0, 255, 115)
}

.node circle,
.node rect {
  fill: rgb(0, 255, 115);
  stroke: rgb(0, 255, 115);
  ;
}

.node text {
  fill: rgb(0, 255, 115);
  margin-left: 1em;
  transform: rotate(-90deg);
}

path.link {
  fill: none;
  /* stroke: rgb(0, 255, 115);; */
  stroke: rgb(134, 134, 134);
}

#creds img {
  /* display: inline-block; */
  max-width: 20px;
  height: 20px;
}

#creds a {
  color: rgb(0, 255, 115);
  background-color: transparent;
  text-decoration: none;
}

#creds {
  align-items: center;
  align-content: center;
  justify-content: center;
}

#creds div.ul {
  justify-content: center;
  align-items: left;
  text-align: left;
  /* background-color: rgb(40, 40, 40); */
  flex-basis: 300px;
  flex-grow: 0;
  flex-shrink: 1;
  height: 300px;
  width: 66%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: auto;
}

#creds div.li {
  /* outline: 1px solid rgb(0, 255, 162); */
  font-size: 1em;
  /* margin-left: 50px; */
  /* flex: 1 1 auto; */
  flex: 1;
}

footer {
  margin-top: 10px;
  text-align: center;
  justify-content: center;
  font-size: 10px;
  height: 36px;
}

svg {
  margin: auto;
  background-color: rgb(33, 33, 33);
}